import {Injectable, Pipe} from '@angular/core';

/*
 Generated class for the Conversion pipe.

 See https://angular.io/docs/ts/latest/guide/pipes.html for more info on
 Angular 2 Pipes.
 */
@Pipe({
  name: 'conversion'
})
@Injectable()
export class Conversion {

  /**
   * 
   * @param sTot true false
   * @returns {string}
   */
  transform(str: string = '', sTot: boolean = true) {
    let newStr = '';
    for (let s of str) {
      let index = (sTot ? Conversion.s() : Conversion.t()).indexOf(s);
      newStr += index == -1 ? s : (sTot ? Conversion.t() : Conversion.s()).charAt(index);
    }
    return newStr;
  }

  static t() {
    return '';
  }

  static s() {
    return '';
  }

}



