import {Component, Input} from '@angular/core';
import {NavController, NavParams, ModalController, IonicPage} from 'ionic-angular';
import {SearchAddress} from "../search-address/search-address";
import {Navigation} from "../navigation/navigation";
import {NativeService} from "../../../providers/NativeService";
declare var AMap;
/**
 * Generated class for the MapLocation page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-map-location',
  templateUrl: 'map-location.html',
})
export class MapLocation {
  map: any;//
  mapIsComplete: boolean = false;//
  isPositioning: boolean = false;//
  marker: any;//
  showIonFab: boolean = false;//
  @Input()
  params = {
    draggable: true,//;
    click: false,//
    searchBar: true,//
    navigation: true,
    address: '',//
    position: {
      lng: '',
      lat: ''
    },//
    lnglatXY: {}
  };

  constructor(public navCtrl: NavController,
              public modalCtrl: ModalController,
              public  nativeService: NativeService,
              public navParams: NavParams) {
  }

  ngAfterContentInit() {
    this.loadMap();
    setTimeout(() => {
      if (!this.map) {
        this.loadMap();
      }
    }, 3000);
  }

  //
  private loadMap() {
    let that = this;
    try {
      that.map = new AMap.Map('map-share', {
        view: new AMap.View2D({//
          zoom: 14, //
          rotateEnable: true,
          showBuildingBlock: true,
          baseRender: 'd'
        })
      });

      that.map.on('complete', function () {
        that.mapIsComplete = true;
        AMap.plugin(['AMap.ToolBar', 'AMap.Scale'], function () {//
          that.map.addControl(new AMap.ToolBar());
        });
        if (that.params.position && that.params.position.lat && that.params.position.lng) { //
          that.drawMarker(that.params.position);
        } else if (!that.params.position && that.params.address) {
          //,
          that.locationSearch();
        } else {
          //addressposition
          that.mapLocation();
        }

        //
        if (that.params.click) {
          that.map.on('click', function (e) {
            let position = {
              lng: e.lnglat.getLng(),
              lat: e.lnglat.getLat()
            };
            that.drawMarker(position);
          });
        }

      });
      window['HomeAMap'] = this.map;
    } catch (err) {
      that.mapIsComplete = false;
      that.nativeService.showToast(',.')
    }
  }

//,(+)
  private locationSearch() {
    let that = this;
    let locationSearchModal = that.modalCtrl.create(SearchAddress, {address: that.params.address});
    locationSearchModal.present();
    locationSearchModal.onDidDismiss(item => {
      if (item) {
        this.drawMarker(item.location, item.name);
      }
    })
  }

//
  mapLocation() {
    let that = this;
    that.isPositioning = true;
    that.nativeService.getUserLocation().subscribe(position => {
      that.drawMarker(position);
      that.isPositioning = false;
    }, () => {
      that.isPositioning = false;
    });
  }

//
  private drawMarker(position, addressName: string = '') {
    let that = this;
    that.params.lnglatXY = new AMap.LngLat(position.lng, position.lat);
    that.map.clearMap();

    //position
    if (that.params.searchBar) {
      if (addressName) {
        that.params.address = addressName;
      } else {
        that.geocoder(that.params.lnglatXY);
      }
    }


    that.marker = new AMap.Marker({
      map: that.map,
      draggable: that.params.draggable,//
      position: that.params.lnglatXY,
    });

    //
    if (that.params.navigation) {
      if (that.marker) {
        that.showIonFab = true;
      }
    }

    //
    that.marker.on('dragend', function (e) {
      let position = {
        lng: e.lnglat.getLng(),
        lat: e.lnglat.getLat()
      };
      that.drawMarker(position);
    });
    that.map.setFitView();
  }

  //
  private geocoder(position) {
    let that = this;
    let geocoder = new AMap.Geocoder({
      radius: 1000,
      extensions: "all"
    });
    geocoder.getAddress(position, function (status, result) {
      if (status === 'complete' && result.info === 'OK') {
        //:
        let addressComponent = result.regeocode.addressComponent;
        that.params.address = addressComponent.district + addressComponent.township +
          addressComponent.street + addressComponent.streetNumber;
      } else {
        that.params.address = '';
      }
    });
  }

//
  mapNavigation(navigationType) {//1,2,3
    let markerPosition = this.marker.getPosition();
    if (!markerPosition) {
      this.nativeService.showToast('');
      return;
    }
    let modal = this.modalCtrl.create(Navigation, {
      'navigationType': navigationType,
      'markerLocation': {'lng': markerPosition.lng, 'lat': markerPosition.lat}
    });
    modal.present();
  }

}



