import {Injectable} from '@angular/core';

@Injectable()
export class GlobalData {

  private _userId: string;//id
  private _username: string;//
  private _fullName: string;//
  private _token: string;//token

  //httploading,:true,loading,false
  private _showLoading: boolean = true;

  //app.0,app
  private _updateProgress: number = -1;

  get userId(): string {
    return this._userId;
  }

  set userId(value: string) {
    this._userId = value;
  }

  get username(): string {
    return this._username;
  }

  set username(value: string) {
    this._username = value;
  }

  get fullName(): string {
    return this._fullName;
  }

  set fullName(value: string) {
    this._fullName = value;
  }

  get token(): string {
    return this._token;
  }

  set token(value: string) {
    this._token = value;
  }

  get showLoading(): boolean {
    return this._showLoading;
  }

  set showLoading(value: boolean) {
    this._showLoading = value;
  }

  get updateProgress(): number {
    return this._updateProgress;
  }

  set updateProgress(value: number) {
    this._updateProgress = value;
  }
}



